import React from "react";
import styled from "styled-components";

const Modal = ({ showModal, setShowModal }) => {
  return (
    <>
      {showModal ? (
        <Wrap>
          <TopWrap>
            <h1>IMPORTANT</h1>
            <Close onClick={() => setShowModal(false)}>Close</Close>
          </TopWrap>
          <h3 style={{ fontWeight: "200" }}>
            Thank you for visiting our website! Unfortunately, it has not yet
            been properly calibrated for mobile and tablet devices. If you are
            visiting on a mobile device, please consider switch to a device that
            has a 16:9 aspect ratio for the best user experience.
          </h3>
        </Wrap>
      ) : null}
    </>
  );
};

const Wrap = styled.div`
  position: fixed;
  background: white;
  width: 350px;
  padding: 20px;
  bottom: 2%;
  right: 2%;
  z-index: 1000;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  border-radius: 20px;
`;

const TopWrap = styled.div`
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
`;

const Close = styled.h4`
  color: #aaa;
  cursor: pointer;
  margin-bottom: 20px;

  &:hover {
    color: black;
  }
`;
export default Modal;
